/** Media Queries **/
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  color: #2d3137;
}
body::-webkit-scrollbar {
  background-color: rgba(232, 238, 243, 0.8549019608);
  width: 0.8rem;
}
body::-webkit-scrollbar-thumb {
  background: #6f6be4;
  border-radius: 1rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  text-align: center;
}

p {
  margin: 0;
  font-size: 1.6rem;
}

.header {
  padding: 4rem;
}
.header h1 {
  font-weight: 900;
}
.header span {
  color: #6b67e3;
}

#root {
  background-color: rgba(232, 238, 243, 0.8549019608);
}

@media (min-width: 768px) {
  .content {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.formulary {
  padding: 2rem 1rem;
}
.formulary .description {
  margin-top: 1.6rem;
}
.formulary .description span {
  color: #6b67e3;
  font-weight: 500;
}

form {
  border-radius: 0.5rem;
  background-color: #ffffff;
  margin-top: 2rem;
  padding: 2rem 4rem;
}
form .section {
  display: flex;
}
@media (max-width: 1024px) {
  form .section {
    margin-bottom: 5rem;
    flex-direction: column;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  form .section {
    margin-bottom: 2rem;
    align-items: center;
    justify-content: space-between;
  }
}
form .section:last-child {
  margin-bottom: 0;
}
form input:not([type=submit]),
form textarea {
  width: 100%;
  max-width: 25rem;
  border: none;
  outline: none;
  padding: 1rem 0 0.3rem 0;
  background-color: transparent;
  border-bottom: 1px solid #bfbdf3;
  transition: 0.2s;
  color: #6460a3;
  font-size: 1.4rem;
  line-height: 1.8rem;
  font-weight: 300;
}
form input:not([type=submit]):hover,
form textarea:hover {
  border-bottom: 1px solid #6b67e3;
}
form textarea {
  height: 5rem;
  resize: vertical;
}
form textarea::-webkit-scrollbar {
  width: 0.2rem;
}
form textarea::-webkit-scrollbar-thumb {
  background: #6b67e3;
  border-radius: 1rem;
}
form .button {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: #6b67e3;
  color: #ffffff;
  transition: 0.3s;
  width: 100%;
}
form .button:hover {
  background-color: #413cdb;
  cursor: pointer;
}

.pacientes {
  padding: 2rem 1rem;
}
@media (min-width: 768px) {
  .pacientes {
    height: 100vh;
    overflow-y: scroll;
  }
  .pacientes::-webkit-scrollbar {
    width: 0.2rem;
  }
  .pacientes::-webkit-scrollbar-thumb {
    background: #9592eb;
    border-radius: 1rem;
  }
}
.pacientes .description {
  margin-top: 1.6rem;
}
.pacientes .description span {
  color: #6b67e3;
  font-weight: 500;
}
.pacientes .paciente {
  border-radius: 0.5rem;
  background-color: #ffffff;
  margin-top: 2rem;
  padding: 2rem 4rem;
}
.pacientes .paciente p {
  margin-bottom: 2rem;
  font-weight: 500;
  color: #6b67e3;
}
.pacientes .paciente p:last-child {
  margin-bottom: 0;
}
.pacientes .paciente span {
  font-weight: 300;
  color: #2d3137;
}

.alert {
  width: 100%;
  padding: 1rem 0;
  transition: 0.3s;
  margin-bottom: 1rem;
}
.alert.error {
  color: #af4735;
  border-left: 0.2rem solid #c9604e;
  padding-left: 0.5rem;
}
.alert.error:hover {
  cursor: pointer;
  background-color: #f4dedb;
  border-left: 0.5rem solid #c9604e;
  padding-left: 1rem;
  transform: translateX(1rem);
}

.buttons {
  display: flex;
}
.buttons .edit {
  border: none;
  border-radius: 0.5rem;
  display: "inline-block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: rgb(94, 80, 141);
  color: #ffffff;
  transition: 0.3s;
  min-width: 10rem;
}
.buttons .edit:hover {
  background-color: #483e6c;
  cursor: pointer;
}
@media (min-width: 768px) {
  .buttons .edit {
    margin-right: 1rem;
  }
}
.buttons .delete {
  border: none;
  border-radius: 0.5rem;
  display: "inline-block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: #c9604e;
  color: #ffffff;
  transition: 0.3s;
  min-width: 10rem;
}
.buttons .delete:hover {
  background-color: #af4735;
  cursor: pointer;
}
@media (max-width: 768px) {
  .buttons {
    justify-content: space-between;
  }
}